/**
 * @module ui-fx-account-selector-ng
 * @description
 * UI component for selecting user account.
 *
 * @example
 * // In an extension:
 * // file: scripts/index.js
 * import uiBbAccountSelector from 'ui-fx-account-selector-ng';
 *
 * export const dependencyKeys = [
 *   uiBbAccountSelector,
 * ];
 *
 * // file: templates/template.ng.html
 * <ui-fx-account-selector-ng
 *   ng-model="$ctrl.payment.from"
 *   accounts="$ctrl.accountsList"
 *   ng-change="$ctrl.onAccountChange()">
 * </ui-fx-account-selector-ng>
 *
 * // multiple selection
 * <ui-fx-account-selector-ng
 *   ng-model="$ctrl.accounts.selected"
 *   accounts="$ctrl.accounts.list"
 *   multiple="true"
 *   ng-change="$ctrl.onAccountChange($item)"
 *   labels="{
 *     noAccounts: ('account.select.label.no.accounts' | i18n),
 *     account: ('account.select.label.account' | i18n),
 *     accounts: ('account.select.label.accounts' | i18n),
 *     allAccounts: ('account.select.label.all.accounts' | i18n),
 *     selectedAccounts: ('account.select.label.selected.accounts' | i18n),
 *   }">
 * </ui-fx-account-selector-ng>
 */
import angular from 'vendor-bb-angular';

import uiBbDropDownSelectKey from 'ui-bb-dropdown-select-ng';
import uiBbFormatAmountKey from 'ui-bb-format-amount-ng';
import uiBbSearchBoxKey from 'ui-bb-search-box-ng';
import uiBbListKey from 'ui-bb-list-ng';

import uiFxAccountSelectorComponent from './selector.component';

/**
 * @name default
 * @type {string}
 * @description The angular module name
 */
export default angular.module('ui-fx-account-selector-ng', [
  uiBbDropDownSelectKey,
  uiBbFormatAmountKey,
  uiBbSearchBoxKey,
  uiBbListKey,
])
  .component('uiFxAccountSelectorNg', uiFxAccountSelectorComponent)
  .name;



